<template>
  <div>
    <h1>Reaction Timer</h1>
    <button @click="playing" v-show="!button1">Start</button>
    <button @click="restart" v-show="button">Back To Start</button>
    <h1 v-if="playingStatus">You have started paying</h1>
    <BlockComp
      v-if="playingStatus"
      :delay="delay"
      @end="endGame"
      :score="score"
    />
    <ResultsComp :score="score" :status="win" />
  </div>
</template>

<script>
import BlockComp from "./components/BlockComp.vue";
import ResultsComp from "./components/ResultsComp.vue";

export default {
  name: "App",
  components: { BlockComp, ResultsComp },
  data() {
    return {
      playingStatus: false,
      score: null,
      button: false,
      button1: false,
      win: false,
    };
  },

  methods: {
    playing() {
      this.delay = 2000 + Math.random() * 5000;
      console.log(this.delay);
      this.playingStatus = true;
      this.button1 = true;
    },
    endGame(e) {
      this.score = e;
      this.playingStatus = false;
      this.win = true;
      this.button = !this.button;
    },
    restart() {
      this.win = false;
      this.button1 = false;
      this.button = false;
    },
  },
};
</script>

